'use strict';
import { getIndicatorProfileUrl, getIndicatorCodelistID } from '../lib/settings';

const getKeyStartsWith = (node, startsWith) => {
  const k = Object.keys(node);
  if (k == null) return [];
  return k.filter(x => x.startsWith(startsWith));
};

export const mapStructure = (s, lang) => {
  console.log('mapStructure');
  console.log('mapStructure');
  console.log('mapStructure');
  console.log(s);

  //renameKey(s.meta, 'contentLanguages', 'content-languages');

  //Preferred language is in the list? put it in the first position
  /*if (s.meta['content-languages'].includes(lang)) {
    s.meta['content-languages'].sort(function(a, b) {
      if (a == lang) return -1;
      return 0;
    });
  }*/

  //ConceptSchemes
  /*s.data.conceptSchemes.forEach(cs => {
    cs.concepts.forEach(c => {
      c.links.forEach(l => {
        l['type'] = 'conceptScheme';
      });
      c.name = c.names;
    });
  });*/
  const m = { data: {} };
  const node_catScheme =
    s.references[
      getKeyStartsWith(s.references, 'urn:sdmx:org.sdmx.infomodel.categoryscheme.CategoryScheme')[0]
    ];

  //conceptSchemes
  m.data.conceptSchemes = [];
  node_catScheme.items.forEach(n => {
    const toAdd = {};
    toAdd.id = n.id;
    toAdd.name = n.name;
    m.data.conceptSchemes.push(toAdd);
  });

  console.log(node_catScheme);
  console.log('mapped');
  console.log(m);
  //renameKey(s.data, 'conceptschemes', 'conceptSchemes');

  //codelists
  s.data.codelists.forEach(cl => {
    cl.name = cl.names;
    const parents = new Set();

    cl.codes.forEach(c => {
      c.name = c.names;
      addLinkAnnotation(cl.id, c);
      if (c.parent) parents.add(c.parent);
    });

    if (parents.size == 1) {
      cl.codes.forEach(c => {
        if (c.parent) delete c.parent;
      });
    }
  });

  //datastructures
  //renameKey(s.data, 'datastructures', 'dataStructures');

  // s.data.dataStructures[0].dataStructureComponents.dimensionList.dimensions.splice(0,1);

  //dataflows
  s.data.dataflows[0].name = s.data.dataflows[0].names;

  return s;
};

const addLinkAnnotation = (cl_id, code, indicator_codelist_id) => {
  if (cl_id != getIndicatorCodelistID) return;
  if (!getIndicatorProfileUrl || getIndicatorProfileUrl == '') return;
  if (typeof code.annotations == 'undefined') code.annotations = [];
  code.annotations.push({ type: 'link', value: getIndicatorProfileUrl + '/' + code.id });
};

const renameKey = (object, oldKey, newKey) => {
  if (!object[oldKey]) return;
  object[newKey] = object[oldKey];
  delete object[oldKey];
};

/*
DATA
*/
export const convertName2 = (name, lang) => {
  const ret = {};
  ret[lang] = name;
  return ret;
};

export const map_struct_component = (comp, lang) => {
  if (comp) {
    comp.forEach(c => {
      c.name = convertName2(c.name, lang);
      c.values.forEach(v => {
        v.name = convertName2(v.name, lang);
      });
    });
  }
};

export const mapData = (d, lang, roundData) => {
  console.log('MAPPING DATA');
  console.log('MAPPING DATA');
  console.log('MAPPING DATA');
  console.log(d);

  d.meta['content-languages'] = [lang];

  d.data.structure.name = d.data.structure.names;

  map_struct_component(d.data.structure.dimensions.dataset, lang);
  map_struct_component(d.data.structure.dimensions.series, lang);
  map_struct_component(d.data.structure.dimensions.observation, lang);
  map_struct_component(d.data.structure.attributes.dataset, lang);
  map_struct_component(d.data.structure.attributes.series, lang);
  map_struct_component(d.data.structure.attributes.observation, lang);

  //Round the number:
  //if the value < 1000 then roud to 4 digits
  if (roundData) {
    for (let [key, value] of Object.entries(d.data.dataSets[0].observations)) {
      if (value && value[0] && !isNaN(value[0])) {
        try {
          if (value[0] > 1000) value[0] = Number.parseFloat(value[0]).toFixed(0);
          else value[0] = Number.parseFloat(value[0]).toFixed(roundData);
        } catch (err) {
          //Empty
        }
      }
    }
  }
  //console.log("RETURNING")
  //console.log(ret)

  return d;
};
